    updateScroll() {
      var limit = Math.max( document.body.scrollHeight, document.body.offsetHeight, 
                   document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight ) - window.innerHeight;
      
      var position = document.documentElement.scrollTop || document.body.scrollTop;

      var current = Math.floor(position / (limit / 3)) + 1

      console.log(current)

      this.scrollPosition = current;

      // var nav = document.querySelector("#navigation-container").getBoundingClientRect();
      // var bulb = document.querySelector("#bulb-container img");

      var currentPercent = Math.floor(position / (limit / 100))

      console.log("Current % ", currentPercent)

    },
    setDetail(name) {
      switch (name) {
        case "magnus":
          this.detail = this.magnus
          break;
        case "proj1":
          this.detail = this.proj1
          break;
        case "proj2":
          this.detail = this.proj2
          break;
        case "proj3":
          this.detail = this.proj3
          break;
        case "proj4":
          this.detail = this.proj4
          break;
      }

    },
    getImgUrl(name) {
      switch (name) {
        case "magnus":
          return require('./assets/' + this.magnus.url);
        case "proj1":
          return require('./assets/' + this.proj1.url);
        case "proj2":
          return require('./assets/' + this.proj2.url);
        case "proj3":
          return require('./assets/' + this.proj3.url);
        case "proj4":
          return require('./assets/' + this.proj4.url);
      }
    },
    toTop() {
      document.body.scrollTop = 0;
      document.documentElement.scrollTop = 0;
    },
    toggleMenu() {
      this.menuOpen = !this.menuOpen
    }

  },
  watch: {
    scrollPosition: function() {
      
      this.aboutMe = false;
      this.programming = false;
      this.design = false;


      switch (this.scrollPosition) {
        case 1:
          this.aboutMe = true;
          this.sectionMessage = "Welcome to my portfolio friend!"
          break;
        case 2:
          this.programming = true;
          this.sectionMessage = "Programming Projects."
          break;
        case 3:
          this.design = true;
          this.sectionMessage = "Design Projects."
          break;
      }
      
    }
  },
